export default function Page1() {
  const [animNumber, setAnimNumber] = useState(0);
  const [altRevAnim, setAltRevAnim] = useState(false);
  const handlePrev = () => {
    if (animNumber > 0) {
      setAnimNumber(animNumber - 1);
    }
  };

  const handleNext = () => {
    if (animNumber < animationsArray.length - 1) {
      setAnimNumber(animNumber + 1);
    }
  };

  // const handleAltRev = (event) => {
  //   setAltRevAnim(!altRevAnim);
  // };

  return (
    <StyleRoot>
      <div>
        <div className="controls">
          {/* <label>
            <input
              type="checkbox"
              onClick={handleAltRev}
              checked={altRevAnim}
            />
            Alternate Reverse
          </label> */}
          <button onClick={handlePrev}>Prev</button>
          <div>
            {animNumber + 1}. {animationNames[animNumber]}
          </div>
          <button onClick={handleNext}>Next</button>
        </div>
        <div className="animation-divs">
          <AnimationDiv
            revAlt={altRevAnim}
            anim={animationsArray[animNumber]}
          />
        </div>
      </div>
    </StyleRoot>
  );
}
