import Radium, { StyleRoot } from "radium";
import React from "react";

const styles = {
  desc: {
    color: "red",
    ":hover": {
      color: "white",
      background: "red",
      cursor: "pointer"
    },
    ":focus": {
      color: "yellow",
      background: "orange"
    },
    "@media (max-width: 420px)": {
      fontSize: 75
    }
  }
};

export default function Page2() {
  return (
    <div>
      <StyleRoot>
        <h2 className="page1-heading">How To Set-up Radium And Animations</h2>
        <div>
          <ol>
            <li>
              Install the dependencies{" "}
              <span className="sub-title-name">'radium'</span> and{" "}
              <span className="sub-title-name">'react-animations'</span> by
              using{" "}
              <span className="sub-title-code">
                'npm install <i>dependencyname</i>'
              </span>{" "}
              in your command prompt in the folder where{" "}
              <span className="sub-title-file">'package.json'</span> is located.
            </li>
            <li>
              Import the modules{" "}
              <span className="sub-title-code">
                Radium, {"{StyleRoot}"} from 'radium'
              </span>{" "}
              and{" "}
              <span className="sub-title-code">
                {"{"}
                <i>animationname</i>
                {"}"} from 'react-animations'{" "}
              </span>
              in the component file where you want to use animations.
            </li>
            <li></li>
          </ol>
        </div>
        <div style={{ textAlign: "center" }}>
          <button style={styles.desc}>Demo</button>
        </div>
      </StyleRoot>
    </div>
  );
}
