import React from "react";
import { Link } from "react-router-dom";

const styles = {
  header: {
    width: "100%",
    height: "20px",
    padding: "10px 0",
    background: "rgba(56, 77, 139, 1)",
    color: "white",
    position: "fixed",
    top: "0",
    boxShadow: "0 1px 8px 3px rgba(27, 40, 78, 0.7)"
  },
  homeLink: {
    color: "white",
    position: "absolute",
    right: "15px"
  }
};

function Header() {
  return (
    <div className="header" style={styles.header}>
      <span style={{ paddingLeft: "15px" }}>Title</span>
      <Link to="/" className="home-link" style={styles.homeLink}>
        Home
      </Link>
    </div>
  );
}

export default Header;
