import Radium, { StyleRoot } from "radium";
import React from "react";

function rand255() {
  return Math.floor(Math.random() * 255);
}

function randRGBColor() {
  return "rgb(" + rand255() + "," + rand255() + "," + rand255() + ")";
}

function AnimationDiv(props) {
  let styling = {
    width: "200px",
    height: "300px",
    background: randRGBColor(),
    animation: "3s infinite " + (props.revAlt && " alternate-reverse"),
    margin: "50px",
    animationName: Radium.keyframes(props.anim, "a" + props.id),
    zIndex: -1
  };
  return (
    <StyleRoot>
      <div style={styling}></div>
    </StyleRoot>
  );
}

export default AnimationDiv;
