import React from "react";

import { fadeInDown, zoomInDown } from "react-animations";

import Radium, { StyleRoot } from "radium";
import { Link } from "react-router-dom";

const styles = {
  box: {
    fontSize: "75px"
  },
  zoomInDown: {
    animation: "2s 1",
    animationName: Radium.keyframes(zoomInDown, "zoomInDown")
  },
  fadeIn: {
    animation: "2s 0s 1",
    animationName: Radium.keyframes(fadeInDown, "fadeInDown")
  }
};

export default function MainPage() {
  return (
    <div className="App">
      <StyleRoot>
        <h1 style={[styles.box, styles.zoomInDown]}>Radium</h1>
        <h2 style={styles.fadeIn}>Radium and React-Animations</h2>

        <div className="page-links btn">
          <Link to="/page1">Animations Demo</Link>
          <Link to="/page2">Setup and Working</Link>
          <Link to="/page3">More Benefits of Radium</Link>
          <Link to="/page4">Page 4</Link>
        </div>
      </StyleRoot>
    </div>
  );
}
